import React, { useEffect } from 'react'
import { MapContainer, useMap, GeoJSON, FeatureGroup } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import * as L from "leaflet";
import India from "../data/India.json";


const IndiaMap = ({selectedIndex,setMapData,setMapCenter,setMapZoom,setWindowWidth}) => {

    const FitBoundsExample = () => {
        const map = useMap();
        useEffect(() => {
            if (India  && selectedIndex===0) {
                setMapData(India)
                const geoJSONLayer = L.geoJSON(India);
                const geoJSONBounds = geoJSONLayer.getBounds();
                map.fitBounds(geoJSONBounds);
                map.setMaxBounds(geoJSONBounds);
    
                const center = geoJSONBounds.getCenter();
                setMapCenter([center.lat, center.lng]);
    
                const boundsZoom = map.getBoundsZoom(geoJSONBounds);
                setMapZoom(boundsZoom);
            }
        }, [India]);
    
        return null;
    };

    
    return (
        <>
            <MapContainer
                style={setWindowWidth()}
                attributionControl={false}
                interactive={false}
                zoomControl={false}
                scrollWheelZoom={false}
                zoomDelta={0.25}
                zoomSnap={0}
                minZoom={-1}
                dragging={false}
                keyboard={false}
                touchZoom={false}
                doubleClickZoom={false}
                boxZoom={false}
                tap={false}
                crs={L.CRS.EPSG4326}
            >
                <FitBoundsExample />
                <FeatureGroup>
                    <GeoJSON
                        data={India.features}
                        style={{
                            fillColor: "#84857e",
                            fillOpacity: 0.5,
                            weight: 1,
                            color: "whitesmoke",
                            interactive: false,
                        }}
                    />
                </FeatureGroup>
            </MapContainer>
        </>
    )
}

export default IndiaMap